import React, {useEffect} from 'react'
import './Navbar.css'
import Aos from 'aos'
import 'aos/dist/aos.css'
import {Link} from 'react-router-dom'
export function Navbar(){
    useEffect(()=>{
        Aos.init({duration:2000});
    },  
    []);



    return(
        <nav className="navbar">
            <ul className="menu">
               <Link className="linkitem" to="/home"> <li className="menuitem">Home</li></Link>
               <Link className="linkitem" to="/films"><li className="menuitem">Films</li></Link>
               <Link className="linkitem" to="/writings"> <li className="menuitem">Writings</li></Link>
               <Link className="linkitem" to="/books"><li className="menuitem">Books</li></Link>
               <Link className="linkitem" to="/about"><li className="menuitem">About</li></Link>
            </ul>
            
            <div data-aos="fade-right" data-aos-duration="800"  data-aos-delay="1200"   
    data-aos-once="true" className="cowandmilk"><span className="milk">ü•õ</span>üêÑ</div>
        </nav>
    )
}